.timer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: var(--primary);
}

.timer p {
  font-size: 1.5rem;
  opacity: 0.6;
}

.timer h1 {
  font-size: 5rem;
  font-weight: 700;
  margin: 20px auto;
}

.timer button {
  font-size: 1.25rem;
  font-family: inherit;
  border: 0;
  padding: 8px 12px;
  min-width: 100px;
  max-width: 130px;
  width: 100%;
  border-radius: 5px;
  background: var(--primary);
  color: var(--secondary);
  cursor: pointer;
}
